import React, {useRef, useState}from 'react'
import './assets/css/Registration-Form-with-Photo.css'
import { useAuth } from './AuthContext';
import {createUserWithEmailAndPassword, onAuthStateChanged} from 'firebase/auth'
import { auth} from '../Firebase'
export default function Register() {
    const[registerEmail, setRegisterEmail] = useState("")
    const[registerPassword, setRegisterPassword] = useState("")
    const[loginEmail, setLoginEmail] = useState("")
    const[loginPassword, setLoginPassword] = useState("")
    const register = async() => {
        try{
            const user = await createUserWithEmailAndPassword(auth, registerEmail, registerPassword);
            console.log("hellow")
        }catch(error){
            alert(error.message)
            console.log(error.message)
        }
    };
    return (
        <section class="register-photo">
        <div class="form-container">
            <div class="image-holder"></div>
            
                <h2 class="text-center"><strong>Create</strong> an account.</h2>
                <div class="mb-3"><input class="form-control" type="email" name="email" placeholder="Email" onChange={(event) => {setRegisterEmail(event.target.value)}}/></div>
                <div class="mb-3"><input class="form-control" type="password" name="password" placeholder="Password" onChange={(event) => {setRegisterPassword(event.target.value)}}/></div>
                <div class="mb-3"><input class="form-control" type="password" name="password-repeat" placeholder="Password (repeat)"/></div>
                <div class="mb-3">
                    <div class="form-check"><label class="form-check-label"><input class="form-check-input" type="checkbox"/>I agree to the license terms.</label></div>
                </div>
                <div class="mb-3"><button class="btn btn-primary d-block w-100" onClick={register}>Sign Up</button></div><a class="already" href="#">You already have an account? Login here.</a>
        </div>
    </section>
    )
} 
